import axios from "axios";
import { Course, LoginReq, LoginResp } from "../pages/Course";
import { makeAutoObservable, runInAction } from "mobx";
import { load } from "../router/MyRouter";
import { Navigate, RouteObject } from "react-router-dom";

class RoutesStore {
  // dynamicRoutes: Route[] = [];

  token: string = ""; //令牌
  state: string = "pending";
  message: string = "";
  async login(login: LoginReq) {
    this.state = "pending";
    const resp1 = await axios.post<LoginResp>(
      `http://localhost:8080/login`,
      login
    );
    console.log(resp1);
    if (resp1.data.code === 200) {
      // const resp2 = await axios.get<MenuAndRoute>(``);
      runInAction(() => {
        // this.dynamicRoutes = resp2.data.routeList;
        //存数据
        // localStorage.setItem(
        //   "dynamicRoutes",
        //   JSON.stringify(this.dynamicRoutes)
        // );

        this.token = resp1.data.token;
        localStorage.setItem("token", this.token);

        this.state = "done";
        this.message = "ok";
      });
    } else {
      runInAction(() => {
        this.state = "error";
        this.message = "error";
      });
    }
  }

  //   async fetch(username: string) {
  //     const resp = await axios.get<MenuAndRoute>(``);
  //     runInAction(() => {
  //       this.dynamicRoutes = resp.data.routeList;
  //       //存数据
  //       localStorage.setItem("dynamicRoutes", JSON.stringify(this.dynamicRoutes));
  //     });
  //   }

  get routes() {
    const staticRoutes: RouteObject[] = [
      { path: "/login", element: load("Login") },
      { path: "/", element: load("Demo3") },
      // { path: "/404", element: load("A8ERROR") },
      { path: "/main", element: load("Header") },
      // { path: "/*", element: <Navigate to={"/404"}></Navigate> },
    ];
    // staticRoutes[1].children = this.dynamicRoutes.map((r) => {
    //   return {
    //     path: r.path,
    //     element: load(r.element),
    //   };
    // });
    return staticRoutes;
  }

  get username() {
    if (this.token.length === 0) {
      return "";
    }
    const json = atob(this.token.split(".")[1]);
    console.log(JSON.parse(json));
    return JSON.parse(json).loginName;
  }

  constructor() {
    makeAutoObservable(this);
    //取数据
    // const json = localStorage.getItem("dynamicRoutes");
    // this.dynamicRoutes = json ? JSON.parse(json) : [];

    this.token = localStorage.getItem("token") ?? "";
    this.state = "pending";
    this.message = "";
  }

  reset() {
    // localStorage.removeItem("dynamicRoutes");
    // this.dynamicRoutes = [];

    localStorage.removeItem("token");
    this.token = "";
    this.message = "";
    this.state = "pending";
  }
}

export default new RoutesStore();
